.history{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 70px 0 0 0;
  grid-area: m;
  min-height: 1000px;
  .links{
    display: flex;
    align-items: center;
    width: 1200px;
    height: 50px;

    a{
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 4px 10px;
      background-color: #2D5BE3;
      color: white;
      border-radius: 10px;
      margin-right: 10px;
    }
    //a:hover{
    //  box-shadow: 8px 10px 20px 2px #BEC7E3;
    //  transition-duration: 50ms;
    //}

  }
  .list{
    width: 100%;
    max-width: 1200px;
    border-radius: 10px;
    padding: 10px;
    h1{
      font-size: 20px;
      margin-bottom: 10px;
    }
    p{
      margin-top: 20px;
    }

    li{
      list-style: disc !important;
      margin-left: 35px;
    }
  }
}