.aboutTheLyceum{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 70px 0 0 0;
  grid-area: m;
  min-height: 1000px;

  h1{
    margin-top: 30px;
    font-size: 30px;
  }

  h2{
    margin-top: 20px;
    font-size: 20px;
  }

  li, p{
    list-style: disc !important;
    margin-left: 35px;
  }

  .links{
    display: flex;
    align-items: center;
    width: 1200px;
    height: 50px;
    a{
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 4px 10px;
      background-color: #2D5BE3;
      color: white;
      border-radius: 10px;
      margin-right: 10px;
    }
    //a:hover{
    //  box-shadow: 8px 10px 20px 2px #BEC7E3;
    //  transition-duration: 50ms;
    //}

  }
  .list{
    width: 100%;
    max-width: 1200px;
    border-radius: 10px;
    padding: 10px;
    h1{
      font-size: 20px;
      margin-bottom: 10px;
    }
    .scheduleTable {
      width: 400px;
      border: 1px solid #000;
      border-collapse: collapse;

      td {
        border: 1px solid #000;
        padding: 4px;
        text-align: center;

        &.scheduleTime {
          font-size: 16px;
        }
      }
    }
  }
}