.forApplicants{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 70px 0 0 0;
  grid-area: m;
  min-height: 1000px;
  .links{
    display: flex;
    align-items: center;
    width: 1200px;
    height: 50px;

    a{
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 4px 10px;
      background-color: #2D5BE3;
      color: white;
      border-radius: 10px;
      margin-right: 10px;
    }
  }
  .list{
    width: 100%;
    max-width: 1200px;
    border-radius: 10px;
    padding: 10px;
    h1{
      font-size: 20px;
      margin-bottom: 10px;
    }
  }
}